import React from 'react';
import { connect } from 'react-redux';
import { Checkbox, Toast } from 'antd-mobile';
import assign from 'lodash.assign'
import topicon from './../icon/order.png';

const AgreeItem = Checkbox.AgreeItem;

class Stage1 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      first:false,
      next:false,
      AgreeItem:false
    };
  }
  componentDidMount() {
    let _date = assign({},this.state);
    if (this.props.infor.loaddata == null) {
      this.props.router.push('/');
      return
    }else {
      // 
      // this.props.router.push('/s6');
    }


    if (this.props.infor.loaddata.isRead == "N") {
      _date.first = true;
    }
    if (this.props.infor.loaddata.isRead == "Y" || this.props.infor.finaldata.isRead == "Y" ) {
      _date.next = true;
      _date.AgreeItem = true;
    }
    this.setState(_date);
  }
  render() {
    return (
      <div>
        <div className="NavTOP">
          <div style={{
            width: '0.7rem',
            height: '0.7rem',
            background: 'url('+topicon+') center center /  0.7rem 0.7rem no-repeat' }}
            onClick={function(){
              if (this.state.first == false) {
                this.props.router.push('/index');
              }
            }.bind(this)}
          />
          <span></span>
        </div>
        <div className="main">
          <div className="part1">
            <div className="row">1<span className="strong"></span></div>
            <div className="row">2</div>
            <div className="row">3()<span className="strong"></span></div>
            <div className="row">4</div>
            <div className="row">5()<span className="strong"></span></div>
            <div className="row">6<span className="strong"></span></div>
            <div className="row">7</div>
            <div className="row">8()</div>
            <div className="row">9<span className="strong"></span></div>
            <div className="row">10</div>
            <div className="row">11<span className="strong">18</span></div>
            <div className="row">12<span className="strong"></span></div>
            <div className="AgreeItem">
              <AgreeItem
                checked={this.state.AgreeItem}
                onChange={function (argument) {
                  let _date = assign({},this.state);
                  // 
                  if (this.state.first) {
                    if (argument.target.checked) {
                      _date.next = true;
                      _date.AgreeItem = true;
                    }else {
                      _date.AgreeItem = false;
                      _date.next = false;
                    }
                  }
                  this.setState(_date);
                }.bind(this)}>
                <div className="Item"></div>
              </AgreeItem>
            </div>
          </div>
        </div>
        <div className="NavBottom">
          {(function(){
            let _this = this;
            // 
            if (this.state.first) {
              if (this.state.next) {
                return <div className="NextPageActi" onClick={function(){
                  let _data = assign({},_this.props.infor.finaldata);
                  _data.isRead = "Y";
                  _this.props.dispatch({type:'change_infor',data:_data});
                  Toast.loading('Loading...', 0.5, () => {
                    _this.props.router.push('/s2');
                    document.body.scrollTop = document.documentElement.scrollTop = 0;
                  });
                }}></div>
              }else {
                return <div className="NextPage"></div>
              }
            }else {
              // 
                return <div className="NextPageActi" onClick={function(){
                  _this.props.router.push('/index');
                  document.body.scrollTop = document.documentElement.scrollTop = 0;
                }}></div>
            }
          }.bind(this))()}
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state, ownProps) => ({
  infor:state.reducer.infor
});

export default Stage1 = connect(
  mapStateToProps
)(Stage1);



